export default {
	props: {
		vote: "",
	},
	data() {
		return {
			maxPercent: '',

		};
	},
	methods: {

	},
	mounted() {
		for (var i = 0; i < this.vote.optionList.length; i++) {
			if(this.vote.optionList[i].percent > this.maxPercent){
				this.maxPercent = 90/this.vote.optionList[i].percent;
			}

		}
	}
};
